import React,{useEffect,useState} from "react";
import { baseURL } from "../config/config";
const Sponsor = () => {
  const [sponsor,setSponsor] = useState([])
  useEffect(()=>{
    const url = baseURL+"api/sponsor"
    fetch(url).then((response)=>{
        response.json().then((data)=>{
            setSponsor(data)
        })
    })        
},[])
  return (
    <section id="sponsor" className="brand-one my-5 py-5">
      <div className="container">
        <div className="block-title text-center pt-5 mt-3">
          <h2 className="block-title__title">
            <span>Sponsor</span>
          </h2>
        </div>
        <div className="row justify-content-center medpart">
          {sponsor.map((item, index) => {
            return (
              <div
                id={item.idSponsor}
                className={
                  item.ukuranSponsor + " item  text-center d-flex align-items-center justify-content-center item-sponsor"
                }
              >
                <a className="text-center" href={item.link}>
                  <img
                    className="img-fluid w-100  my-2"
                    src={baseURL+`uploads/sponsor/`+item.gambarSponsor}
                    alt={item.nama_sponsor}
                  />
                </a>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};
export default Sponsor;
